import { Component, OnInit, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'app-staffs-attendance',
  templateUrl: './staffs-attendance.component.html',
  styleUrls: ['./staffs-attendance.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class StaffsAttendanceComponent implements OnInit {

  isDataLoaded:boolean = false;
  fromDate:any;

  constructor() { }

  setDate(event) {
    this.fromDate = event;
  }

  setInTime(event,data) {
    data.inTime = event;
  }

  setOutTime(event,data) {
    data.outTime = event;
  }

  setOverTime(event,data) {
    data.overTime = event;
  }

  setLateTime(event,data) {
    data.lateTime = event;
  }

  ngOnInit(): void {
  }

}
